}
.modelPanel button#applyBtn {
  margin: 10px 5px;
  float: right;
  backgound-color: #3c8dbc;
}
.modelPanel .modelWrap::after {
  content: "";
  display: block;
  height: 1px;
  clear: both;
}
.modelPanel .btn {
  float: right;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
}
.modelPanel select.category {
