}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap {
  margin: 0;
  color: #666;
  padding: 6px;
  width: 100%;
  transition: 0.1s linear all;
  border-bottom: 1px solid #eee;
  -webkit-transition: 0.1s linear all;
  position: relative;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap .vline {
  display: none;
