}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap .glyphicon {
  color: #666;
  margin-right: 10px;
  background-color: #fff;
  padding: 2px 2px 2px 3px;
  border: 1px solid #aaa;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap .glyphicon.glyphicon-plus {
  border: 1px solid #aaa;
  padding: 2px 2px 2px 3px;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap .glyphicon.glyphicon-minus {
  border: 1px solid #aaa;
  padding: 2px 3px 2px 2px;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap.active {
  color: #fff;
  background-color: #2EA8FF;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap.active .vline {
  border-left: 1px solid #FFF;
  top: 0;
  height: 34px;
  position: absolute;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap.active .glyphicon.glyphicon-plus {
  border: 1px solid #FFF;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap.active .glyphicon.glyphicon-minus {
  border: 1px solid #FFF;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap.active:hover {
  background-color: #2EA8FF;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap:hover {
  background-color: #eee;
}
.modelPanel .innerWrap .step .filter .dropdowntree .wrap:hover .vline {
  display: none;
  border-left: 1px solid #aaa;
  top: 0;
  height: 34px;
  position: absolute;
}
.modelPanel .innerWrap .step .filter .dropdowntree .labeltxt {
  margin-right: 10px;
