  margin: 120px auto;
  background-color: #fff;
  border: 2px solid #FFF;
  box-shadow: 1px 1px 5px #C5C5C5;
  margin-bottom: 200px;
  width: 800px;
  position: inherit;
  border-radius: 5px;
}
.drawing-area .prof .li_handler button.btn {
  float: right;
  text-shadow: 1px 1px 1px #000;
  margin: 1px 2px 0 0;
  padding: 0 5px;
  font-size: 12px;
  height: 18px;
  background-color: #777;
  color: #fff;
  border-radius: 0px;
}
.drawing-area .prof .li_content {
  position: relative;
}
.drawing-area .prof .li_content .element {
  width: 100%;
  height: 100%;
  position: absolute;
}
.drawing-area .prof .li_content .element.current:before {
  content: '';
  display: block;
  z-index: 999;
  top: -4px;
  left: -4px;
  background-color: rgba(0, 160, 255, 0.3);
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  position: absolute;
  border: 4px solid #009cff;
}
.drawing-area .prof .li_content .loading {
  width: 100%;
  height: 100%;
  background-color: #50575b;
  position: absolute;
  z-index: 99999;
  display: none;
}
.drawing-area .prof .li_content .loading .loadingIcon {
  background-size: cover;
  width: 40px;
  height: 40px;
  display: inline-block;
  background-image: url(../images/loading_spinner.gif);
}
.drawing-area .prof .li_content .reList {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 99;
  display: none;
}
.drawing-area .prof .li_content .reList .resourseSel {
  width: 30%;
  position: absolute;
  top: 10px;
  right: 20px;
  border-radius: 6px;
  background-color: #eee;
  border: 1px solid #ccc;
}
.drawing-area .prof .li_content .reList .resourseSel .addOn {
  line-height: 34px;
  display: inline;
}
.drawing-area .prof .li_content .noData {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  z-index: 1;
  display: none;
}
.drawing-area .prof .li_content .noData table {
  width: 100%;
  height: 100%;
}
.drawing-area .prof .li_content .noData table td {
  text-align: center;
  vertical-align: middle;
  color: white;
}
.drawing-area .prof .li_content .edit {
  display: none;
  width: 100%;
  z-index: 1300;
  position: absolute;
  cursor: move;
  background-color: rgba(0, 0, 0, 0.6);
}
.drawing-area .prof .li_content .edit .noBind-text {
  color: #fff;
  line-height: 30px;
  margin-left: 20px;
}
.drawing-area .prof .li_content .edit::after {
  content: '';
  display: block;
  height: 1px;
  clear: both;
}
.drawing-area .prof .li_content .edit button {
  margin: 2px;
  float: right;
}
.drawing-area .prof .li_content .edit .btn {
  padding: 0;
  padding: 1px 3px;
  background-color: #333;
  color: #fff;
  border-radius: 0;
  text-shadow: 0px 0px 0px #000;
}
.drawing-area .prof #chartHeader {
  background-color: #ECF0F5;
  height: 30px;
}
.drawing-area .prof #chartHeader input {
  color: #333;
  border-style: none;
  width: 100%;
  background-color: transparent;
  line-height: 30px;
  height: 32px;
  margin: 0;
  padding: 0;
  text-align: center;
}
.drawing-area .prof .prof-header {
  width: 100%;
  background-color: #E3E7EA;
  border-bottom: 1px solid #ddd;
  height: 20px;
  cursor: move;
}
.drawing-area .prof .prof-header .closeBtn {
  text-align: center;
  cursor: pointer;
  background-color: #ddd;
  border: 1px solid #888;
  width: 20px;
  height: 20px;
  float: right;
}
.drawing-area .prof ul#sortable {
  min-height: 400px;
}
.drawing-area .prof ul#sortable::after {
  content: '';
  display: block;
  height: 1px;
  clear: both;
}
.drawing-area .prof ul#sortable li.item {
  cursor: pointer;
  list-style: none;
  float: left;
  position: relative;
  background-color: #fff;
  border: 1px dashed transparent;
}
.drawing-area .prof ul#sortable li.item .imageControl {
  position: relative;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #666;
}
.drawing-area .prof ul#sortable li.item .imageControl::before {
  content: '';
  height: 1px;
  display: block;
}
.drawing-area .prof ul#sortable li.item .imageControl canvas {
  position: absolute;
  width: 95%;
  height: 95%;
  z-index: 9999;
}
.drawing-area .prof ul#sortable li.item .imageControl img {
  width: 100%;
}
.drawing-area .prof ul#sortable li.item .imageControl p {
  margin-top: 100px;
  text-align: center;
  color: white;
}
.drawing-area .prof ul#sortable li.item .imageControl button {
  color: #888;
  display: block;
  margin: auto;
}
.drawing-area .prof ul#sortable li.item table.textControl {
  width: 100%;
  height: 100%;
  margin: auto;
  background-color: transparent;
}
.drawing-area .prof ul#sortable li.item table.textControl td {
  text-align: center;
}
.drawing-area .prof ul#sortable li.item table.textControl td p.innerTxt {
  margin: 0;
  padding: 0;
  display: inline-block;
  text-align: center;
}
.drawing-area .prof ul#sortable li.item table.textControl td input.editInput {
  font-size: 16px;
  width: 100%;
  height: 100%;
  text-align: center;
}
.drawing-area .prof ul#sortable li.item .noData {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.6);
  border: 1px dashed #000;
}
.drawing-area .prof ul#sortable li.item .noData p {
  text-align: center;
  font-size: 20px;
  color: white;
  margin-top: 50px;
  background-color: #000;
}
.drawing-area .prof ul#sortable li.item.select {
  background-color: rgba(0, 156, 255, 0.2);
  border: 1px dashed #009cff;
}
.drawing-area .prof ul#sortable li.item.select:hover {
  background-color: rgba(0, 156, 255, 0.3);
  border: 1px dashed #009cff;
}
.drawing-area .prof ul#sortable li.item.placeholder {
  background-color: transparent;
  border: 1px dashed #ccc;
}
.drawing-area .prof ul#sortable li.item .element {
  height: 100%;
  padding: 30px 10px 0 0;
}
.drawing-area .prof ul#sortable li.item.noBind {
  background-color: rgba(0, 0, 0, 0.1);
  border: 1px dashed #009cff;
}
.drawing-area .prof ul#sortable li.item.noBind .edit {
  display: block;
}
.drawing-area .prof ul#sortable li.item.noBind:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border: 1px dashed #009cff;
}
.drawing-area .prof ul#sortable li.item:hover {
  background-color: rgba(0, 156, 255, 0.2);
  border: 1px dashed #009cff;
}
.drawing-area .prof ul#sortable li.item:hover .edit {
  display: block;
}
